<!-- 主要页面 -->
<template>
  <el-container style="width:auto;overflow: visible;">
    <el-header class="header">
      <el-dropdown>
        <div class="head-box">
          <span>{{user.username}}</span>
        </div>
        <el-dropdown-menu slot="dropdown">
          <router-link :to="{name:'information'}">
            <el-dropdown-item>
              个人中心
            </el-dropdown-item>
          </router-link>
          <el-dropdown-item>消息</el-dropdown-item>
          <el-dropdown-item>注销</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>
    <el-main class="main-container">
      <div class="main">
        <router-view></router-view>
      </div>
    </el-main>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      user: {
        usernmae: ""
      }
    };
  },

  components: {},

  computed: {},

  created() {
    localStorage.user ? (this.user = JSON.parse(localStorage.user)) : "";
    console.log(this.user);
  },

  mounted() {},

  methods: {}
};
</script>
<style scoped>
.min-w {
  display: inline-block;
  min-width: 1200px;
  width: 1200px;
  text-align: right;
}
.header {
  /* width: 100%; */
  min-width: 1200px;
  text-align: center;
  background: #409eff;
  line-height: 60px;
  color: #ffffff;
  text-align: right;
}
.head-box {
  color: #ffffff;
}

.main-container {
  text-align: center;
  width: 100%;
  overflow: hidden;
}
.main {
  display: inline-block;
  width: 1160px;
  text-align: left;
}
a {
  text-decoration: none;
}
</style>


