<!-- 修改密码 -->
<template>
    <div>
        <el-form ref="form" :model="user" label-width="80px">
            <el-form-item label="旧密码">
                <el-input v-model="user.oldPassword"></el-input>
            </el-form-item>
            <el-form-item label="新密码">
                <el-input v-model="user.newPassword"></el-input>
            </el-form-item>
            <el-form-item label="确认密码">
                <el-input v-model="user.surePassword"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" size="small" @click="onSubmit">保存</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
export default {
  data() {
    return {
      user: {
        oldPassword: "",
        newPassword: "",
        surePassword: ""
      }
    };
  },

  components: {},

  computed: {},

  created() {},

  mounted() {},

  methods: {}
};
</script>
<style scoped>

</style>


